  background: rgb(37,37,37);
  text-align: center;
  border-radius: 20px 20px 0px 0px;
}

.commentsTittle {
  font-size: 30px;
  color: white;
  /* border-radius: 20px 20px 0px 0px; */
}

.comments-general-container {
  margin-top: 4vh;
  width: 100%;
  height: 40vh;
  background: white;
  border: 1px solid black;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  }

.all-comments-container{
  margin: 10px 5px 10px 15px;
  flex-grow: 1;
  height: 40%;
  padding: 20px;
  border-radius: 20px 10px 10px 20px;
  font-size: 18px;
  overflow-x: auto; 
}

.all-comments-container::-webkit-scrollbar {
  -webkit-appearance: none;
}

.all-comments-container::-webkit-scrollbar:vertical {
  width:10px;
}

.all-comments-container::-webkit-scrollbar-button:increment, .all-comments-container::-webkit-scrollbar-button {
  display: none;
} 

.all-comments-container::-webkit-scrollbar:horizontal {
  height: 10px;
}

.all-comments-container::-webkit-scrollbar-thumb {
  background-color: #797979;
  border-radius: 20px;
}

.all-comments-container::-webkit-scrollbar-track {
  border-radius: 10px;  
}


.comment {
  width: fit-content;
  min-height: 30px;
  background-color: rgba(255, 166, 0, 0.774);
  padding: 2px 10px;
  border-radius: 10px;
  margin: 0px 0px 20px 0px ;
}


.x-alineator{
  display: flex;
}

.authorComment-img {
  width: 75px;
  height: 75px;
  border-radius: 100%;
  background-position: center;
  background-size: cover;
}

.authorComment-img:hover {
  cursor: pointer;
}

.commentContent-container{
  border-radius: 10px;
  margin-left: 10px;
  align-self: center;
}

.comment-author {
  margin-bottom: 0px;
  display: inline-block;
}

.comment-author:hover {
  cursor: pointer;
  text-decoration: underline;
}

.comment-content {
  padding: 0px 10px 10px 40px;
  margin-bottom: 0px;
  line-height: 20px;
}

.editingInput {
  font-size: 18px;
  width: 30vw;
  border-radius: 10px;
  padding: 5px;
  outline: none;
  border: 2px solid black;
}

.sendEditedIcon {
  font-size: 30px;
  margin-left: 6px;
}

.sendEditedIcon:hover {
  cursor: pointer;
}

.modifyCommentIcons-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 19px;
  height: 50px;
  align-self: center;
  margin: 0 1%;
}

.edit-icon:hover {
  color: rgb(0,2,54);
  cursor: pointer;
}

.delete-icon:hover {
  color: red;
  cursor: pointer;
}

.input-container {
  width: 95%;
  display: flex;
  height: 42px;
  margin: 0 auto 20px auto;
  align-self: flex-end;
  border: 3px solid rgb(49, 49, 49);
  border-radius: 5px;
}

.comments-input {
  width: 100% ;
  align-self: flex-end;
  border-radius: 10px;
  outline: none;
  text-align: center;
  padding: 5px 0px;
  border: none;
}

.paperPlane-icon {
  display: flex;
  align-items: flex-end;
  font-size: 30px;
  margin-right: 10px;
  align-self: center;
  transition: all .2s ease;
}

.paperPlane-icon:hover {
  color: rgb(105, 105, 105);
  cursor: pointer;
}

.takeALook-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}


.down-arrow {
  font-size: 70px;
  margin-bottom: 20px;
  transition: all .2s ease;
}

.down-arrow:hover {
  cursor: pointer;
  font-size: 75px;
  margin-bottom: 15px;
}


